  // ================================
  // AI  
  // ================================
  
  async getAllServices(params?: {
    page?: number;
    size?: number;
    category?: string;
    search?: string;
    sort?: string;
    pricing?: string;
  }): Promise<AITool[]> {
    try {
      const queryParams = new URLSearchParams();
      
      if (params?.page !== undefined) queryParams.append('page', params.page.toString());
      if (params?.size !== undefined) queryParams.append('size', params.size.toString());
      if (params?.category) queryParams.append('category', params.category);
      if (params?.search) queryParams.append('search', params.search);
      if (params?.sort) queryParams.append('sort', params.sort);
      if (params?.pricing) queryParams.append('pricing', params.pricing);
  
      const queryString = queryParams.toString();
      const endpoint = queryString ? `/ai-services?${queryString}` : '/ai-services';
      
      const response = await this.request<ApiResponse<ServiceListResponse>>(endpoint);
      
      if (!response.success || !response.data) {
        throw new Error('Invalid response structure');
      }
      
      const serviceList = response.data;
      if (!Array.isArray(serviceList.data)) {
        console.error('  :', serviceList.data);
        return [];
      }
  
      return serviceList.data.map(tool => {
        const categorySlug = getCategorySlug(tool.category?.name || '');
        
        let tagsArray: string[];
        
        if (tool.tags && tool.tags.trim() !== '') {
          tagsArray = [tool.tags.trim()];
        } else {
          tagsArray = [tool.category?.name || ''];
        }
        
        return {
          id: tool.id.toString(),
          name: tool.serviceName,
          category: categorySlug,
          description: tool.description || '',
          features: tool.keywords || [],
          rating: Number(tool.overallRating) || 0,
          tags: tagsArray,
          url: tool.websiteUrl || '',
          releaseDate: tool.launchDate || '',
          company: 'Unknown',
          pricing: this.mapPricingType(tool.pricingType),
          featured: false,
          categoryLabel: tool.category?.name || '',
          roles: [],
          userCount: 0,
          aiRating: Number(tool.overallRating) || 0,
          //   logoUrl ,    fallback
          logoUrl: tool.logoUrl || '/images/Logo/Logo_FINAL.svg',
          serviceImageUrl: '/images/GlassMorphism/Detailpage/Detailpage_Happy.png',
          priceImageUrl: '/images/GlassMorphism/Price/Price_Default.png',
          searchbarLogoUrl: tool.logoUrl || '/images/SearchbarLogo/Logo_FINAL.svg'
        };
      });
    } catch (error) {
      console.error('AI   :', error);
      throw error;
    }
  }

  async getServiceById(id: string): Promise<AIToolDetail | null> {
    try {
      const response = await this.request<ApiResponse<ServiceDetailResponse>>(`/ai-services/${id}`);
      
      if (!response.success || !response.data) {
        console.error('Invalid response structure:', response);
        return null;
      }
      
      const serviceDetail = response.data;
      const toolData = serviceDetail.data;
      
      const categorySlug = getCategorySlug(toolData.category?.name || '');
      
      const toolDetail: AIToolDetail = {
        id: toolData.id,
        serviceName: toolData.serviceName,
        description: toolData.description || '',
        websiteUrl: toolData.websiteUrl || '',
        logoUrl: toolData.logoUrl || '/images/Logo/Logo_FINAL.svg',
        launchDate: toolData.launchDate || '',
        category: {
          id: toolData.category?.id || 1,
          name: toolData.category?.name || '',
          slug: categorySlug
        },
        pricingType: toolData.pricingType || 'FREE',
        pricingInfo: '',
        pricingLink: '',
        overallRating: Number(toolData.overallRating) || 0,
        recommendationScore: Number((toolData as any).recommendationScore ?? (toolData as any).recommendation_score ?? 0) || undefined,
        viewCount: 0,
        bookmarkCount: 0,
        keywords: toolData.keywords?.map(k => k.keyword) || [],
        videos: [],
        reviews: toolData.reviews?.map(r => ({
          id: r.id,
          user: r.user,
          rating: Number(r.rating),
          content: r.content,
          createdAt: r.createdAt,
          updatedAt: r.createdAt
        })) || [],
        //      
        serviceImageUrl: toolData.serviceImageUrl || '/images/GlassMorphism/Detailpage/Detailpage_Happy.png',
        priceImageUrl: toolData.priceImageUrl || '/images/GlassMorphism/Price/Price_Default.png',
        searchbarLogoUrl: toolData.logoUrl || '/images/SearchbarLogo/Logo_FINAL.svg'
      };
      
      return toolDetail;
      
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  //     
  private mapPricingType(pricingType: string): 'free' | 'paid' | 'freemium' {
    switch (pricingType?.toUpperCase()) {
      case 'FREE':
        return 'free';
      case 'PAID':
        return 'paid';
      case 'FREEMIUM':
        return 'freemium';
      default:
        return 'free';
    }
  }

