// []     AI  

import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Header from '../components/Header';
import ToolCard from '../components/ToolCard';
import { apiService } from '../services';
import type { AITool } from '../types';

//    
const getHorizontalPadding = (): number => {
  if (window.innerWidth >= 1440) {
    return 200;
  } else if (window.innerWidth >= 1024) {
    return 64; // lg:px-16
  } else if (window.innerWidth >= 768) {
    return 32; // md:px-8
  } else if (window.innerWidth >= 640) {
    return 24; // sm:px-6
  } else {
    return 16; // 
  }
};

//   BookmarkListResponse   
interface BookmarkedTool {
  id: number;
  aiServiceId: number;
  serviceName: string;
  serviceSummary: string;
  logoUrl: string;
  categoryDisplayName: string;
  pricingType: string;
  tags: string;        //  tags  ( )
  websiteUrl: string;  //  websiteUrl   (officialUrl )
}

const MyPage: React.FC = () => {
  const navigate = useNavigate();
  
  //  
  //        
  const [bookmarkedTools, setBookmarkedTools] = useState<AITool[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeCategory, setActiveCategory] = useState<string>('all');
  
  //   
  const [horizontalPadding, setHorizontalPadding] = useState(getHorizontalPadding());
  
  //      
  useEffect(() => {
    const updatePadding = () => {
      setHorizontalPadding(getHorizontalPadding());
    };
    
    updatePadding();
    window.addEventListener('resize', updatePadding);
    return () => window.removeEventListener('resize', updatePadding);
  }, []);

  //   ToolCard    AITool   
  const convertBookmarkToAITool = (bookmark: BookmarkedTool): AITool => {
    //   
    const mapPricingType = (pricingType: string): 'free' | 'paid' | 'freemium' => {
      switch (pricingType.toUpperCase()) {
        case 'FREE':
          return 'free';
        case 'PAID':
          return 'paid';
        case 'FREEMIUM':
          return 'freemium';
        default:
          return 'freemium';
      }
    };

    //    
    const getCategorySlug = (categoryName: string): string => {
      const categoryMap: Record<string, string> = {
        '': 'chatbot',
        '': 'text', 
        '': 'image',
        '': 'video',
        '/': 'audio',
        '': 'code',
        '3D': '3d',
        '': 'productivity'
      };
      return categoryMap[categoryName] || 'chatbot';
    };

    return {
      id: bookmark.aiServiceId.toString(),
      name: bookmark.serviceName,
      category: getCategorySlug(bookmark.categoryDisplayName),
      description: bookmark.serviceSummary,
      features: [],
      rating: 4.5,
      tags: bookmark.tags ? [bookmark.tags] : [bookmark.categoryDisplayName], //  tags 
      url: bookmark.websiteUrl || '', //  websiteUrl 
      releaseDate: '',
      company: '',
      pricing: mapPricingType(bookmark.pricingType),
      featured: false,
      categoryLabel: bookmark.categoryDisplayName,
      roles: [],
      userCount: 0,
      aiRating: 4.5,
      logoUrl: bookmark.logoUrl || '/images/Logo/Logo_FINAL.svg',
      serviceImageUrl: bookmark.logoUrl || '/images/Logo/Logo_FINAL.svg',
      priceImageUrl: bookmark.logoUrl || '/images/Logo/Logo_FINAL.svg',
      searchbarLogoUrl: bookmark.logoUrl || '/images/Logo/Logo_FINAL.svg'
    };
  };

  //     
  useEffect(() => {
    const loadMyPageData = async () => {
      try {
        //  
        if (!apiService.isAuthenticated()) {
          navigate('/');
          return;
        }

        setLoading(true);
        setError(null);

        //      
        const bookmarksResponse = await apiService.getBookmarksFixed();
        
        //   AITool  
        const convertedTools = bookmarksResponse.bookmarks.map(convertBookmarkToAITool);
        setBookmarkedTools(convertedTools);

      } catch (error) {
        console.error('   :', error);
        setError('  .    .');
        
        //      
        if (error instanceof Error && error.message.includes('')) {
          navigate('/');
        }
      } finally {
        setLoading(false);
      }
    };

    loadMyPageData();
  }, [navigate]);

  //       

  //   
  const categoryChips: { id: string; label: string }[] = [
    { id: 'all', label: '' },
    { id: 'chatbot', label: '' },
    { id: 'text', label: '' },
    { id: 'image', label: '' },
    { id: 'video', label: '' },
    { id: 'audio', label: '/' },
    { id: 'code', label: '' },
    { id: '3d', label: '3D' },
    { id: 'productivity', label: '' }
  ];

  //  
  const filteredTools = activeCategory === 'all'
    ? bookmarkedTools
    : bookmarkedTools.filter((tool) => tool.category === activeCategory);

  if (loading) {
    return (
      <div className="min-h-screen bg-white">
        <Header tabs={[]} activeTab="" onTabChange={() => {}} horizontalPadding={horizontalPadding} fullWidth />
        <div className="flex items-center justify-center pt-20">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-purple-600 mx-auto mb-4"></div>
            <p className="text-gray-600" style={{ fontFamily: 'Pretendard' }}>
                ...
            </p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-white">
        <Header tabs={[]} activeTab="" onTabChange={() => {}} horizontalPadding={horizontalPadding} fullWidth />
        <div 
          className="mx-auto py-8"
          style={{ 
            maxWidth: '1440px',
            paddingLeft: horizontalPadding >= 200 ? `${horizontalPadding}px` : `${horizontalPadding}px`,
            paddingRight: horizontalPadding >= 200 ? `${horizontalPadding}px` : `${horizontalPadding}px`
          }}
        >
          <div className="text-center py-12">
            <div className="text-6xl mb-4"></div>
            <h1 className="text-2xl font-bold text-gray-800 mb-4">   </h1>
            <p className="text-gray-600 mb-6">{error}</p>
            <button
              onClick={() => window.location.reload()}
              className="px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 font-medium"
            >
               
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      <Header tabs={[]} activeTab="" onTabChange={() => {}} horizontalPadding={horizontalPadding} fullWidth />
      <main 
        className="mx-auto py-4"
        style={{ 
          maxWidth: '1440px',
          paddingLeft: horizontalPadding >= 200 ? `${horizontalPadding}px` : `${horizontalPadding}px`,
          paddingRight: horizontalPadding >= 200 ? `${horizontalPadding}px` : `${horizontalPadding}px`
        }}
      >
        {/*   AI    -   */}
        <div className="mt-6 mb-6">
          <h1
            style={{
              fontFamily: 'Pretendard',
              fontWeight: 600,
              fontSize: '32px',
              color: '#202020'
            }}
          >
             AI
          </h1>
          <p
            style={{
              fontFamily: 'Pretendard',
              fontWeight: 500,
              fontSize: '14px',
              color: '#9B9B9B',
              marginTop: '12px'
            }}
          >
             AI   !
          </p>
          {/*   :  AI    */}
          {bookmarkedTools.length > 0 && (
            <div className="mt-8 flex flex-wrap gap-2">
              {categoryChips.map((chip) => {
                const isActive = activeCategory === chip.id;
                return (
                  <button
                    key={chip.id}
                    onClick={() => setActiveCategory(chip.id)}
                    className="inline-flex items-center px-5 py-2 rounded-[12px] transition-colors"
                    style={{
                      borderWidth: '1px',
                      borderColor: isActive ? '#A987E8' : '#BCBCBC',
                      backgroundColor: isActive ? '#F2EEFB' : '#FFFFFF'
                    }}
                  >
                    {isActive && (
                      <img src="/images/Icon/Check/20.svg" alt="" width={20} height={20} style={{ marginRight: '6px' }} />
                    )}
                    <span
                      style={{
                        fontFamily: 'Pretendard',
                        fontWeight: isActive ? 700 : 500,
                        fontSize: '14px',
                        color: isActive ? '#7242C9' : '#5B5B5B'
                      }}
                    >
                      {chip.label}
                    </span>
                  </button>
                );
              })}
            </div>
          )}
        </div>

        {/*  AI  */}
        <section>

          {filteredTools.length === 0 ? (
            /*   -    +  */
            <div className="flex items-center justify-center py-16">
              <div className="flex flex-col items-center text-center">
                <img
                  src="/images/GlassMorphism/Mypage_Save.png"
                  alt=" AI "
                  style={{ width: '80px', height: '80px', marginBottom: '16px' }}
                />
                {bookmarkedTools.length === 0 ? (
                  <>
                    <div
                      style={{
                        fontFamily: 'Pretendard',
                        fontWeight: 600,
                        fontSize: '18px',
                        color: '#202020',
                        marginBottom: '8px'
                      }}
                    >
                       AI .
                    </div>
                    <div
                      style={{
                        fontFamily: 'Pretendard',
                        fontWeight: 400,
                        fontSize: '16px',
                        color: '#202020',
                        lineHeight: 1.6
                      }}
                    >
                      AIMine   AI 
                      <br />
                       !
                    </div>
                  </>
                ) : activeCategory !== 'all' ? (
                  <>
                    <div
                      style={{
                        fontFamily: 'Pretendard',
                        fontWeight: 600,
                        fontSize: '18px',
                        color: '#202020',
                        marginBottom: '8px'
                      }}
                    >
                       AI .
                    </div>
                    <div
                      style={{
                        fontFamily: 'Pretendard',
                        fontWeight: 400,
                        fontSize: '16px',
                        color: '#202020',
                        lineHeight: 1.6
                      }}
                    >
                         AI .
                      <br />
                        .
                    </div>
                  </>
                ) : (
                  <></>
                )}
              </div>
            </div>
          ) : (
            /*  ToolCard   -      */
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredTools.map((tool) => (
                <ToolCard key={tool.id} tool={tool} />
              ))}
            </div>
          )}
        </section>
      </main>
    </div>
  );
};

export default MyPage;