// [AI   ]  AI    - , , BEST , , ,  

import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import type { AITool } from '../types';
import { handleImageError } from '../utils/imageMapping';
import { apiService } from '../services';

interface ToolCardProps {
  tool: AITool;
  rank?: number;
  className?: string;
}

const ToolCard: React.FC<ToolCardProps> = ({ tool, rank, className }) => {
  const [isBookmarked, setIsBookmarked] = useState(false);
  const [bookmarkLoading, setBookmarkLoading] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [statusCheckAttempts, setStatusCheckAttempts] = useState(0);
  const location = useLocation();
  const navigate = useNavigate();
  const handleCardNavigation = () => {
    if (tool.id) {
      navigate(`/tool/${tool.id}`);
    }
  };

  // MyPage 
  const isMyPage = location.pathname === '/mypage';

  //       (   )
  useEffect(() => {
    const checkAuthAndBookmark = async () => {
      console.log(' ToolCard   :', { toolId: tool.id, attempts: statusCheckAttempts });
      
      const authenticated = apiService.isAuthenticated();
      setIsAuthenticated(authenticated);

      if (authenticated && tool.id) {
        try {
          const numericId = parseInt(tool.id);
          if (!isNaN(numericId)) {
            console.log('    :', numericId);
            const bookmarkStatus = await apiService.checkBookmarkStatus(numericId);
            setIsBookmarked(bookmarkStatus);
            console.log('    :', bookmarkStatus);
          }
        } catch (error) {
          console.warn('    :', error);
          
          if (statusCheckAttempts < 2) {
            console.log('    ');
            setTimeout(() => {
              setStatusCheckAttempts(prev => prev + 1);
            }, 1000);
          }
        }
      } else {
        setIsBookmarked(false);
      }
    };

    checkAuthAndBookmark();
  }, [tool.id, statusCheckAttempts, location.pathname]);

  //   
  const handleBookmarkToggle = async (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    
    console.log('   :', { 
      toolId: tool.id, 
      isAuthenticated, 
      currentBookmarkState: isBookmarked 
    });
    
    if (!isAuthenticated) {
      alert('  .');
      return;
    }

    if (bookmarkLoading) {
      console.log('   ...');
      return;
    }

    const numericId = parseInt(tool.id);
    if (isNaN(numericId)) {
      console.error('   tool.id:', tool.id);
      alert(' ID  .');
      return;
    }

    try {
      setBookmarkLoading(true);
      
      //        
      console.log('     ');
      const currentServerState = await apiService.checkBookmarkStatus(numericId);
      console.log('   vs  :', { 
        server: currentServerState, 
        local: isBookmarked 
      });
      
      //       
      if (currentServerState !== isBookmarked) {
        console.log('   ...');
        setIsBookmarked(currentServerState);
      }
      
      //      
      if (currentServerState) {
        console.log('   ');
        await apiService.removeBookmark(numericId);
        setIsBookmarked(false);
        console.log('   ');
        
        // MyPage      (UI  )
        if (isMyPage) {
          window.location.reload();
        }
      } else {
        console.log('   ');
        await apiService.addBookmark(numericId);
        setIsBookmarked(true);
        console.log('   ');
      }
      
    } catch (error) {
      console.error('   :', error);
      
      let errorMessage = '    .';
      
      if (error instanceof Error) {
        if (error.message.includes(' ')) {
          errorMessage = '  .';
          setIsBookmarked(true);
        } else if (error.message.includes(' ') || error.message.includes(' ')) {
          errorMessage = '  .';
          setIsBookmarked(false);
        } else if (error.message.includes('')) {
          errorMessage = ' .';
        } else {
          errorMessage = error.message;
        }
      }
      
      alert(errorMessage);
      
      //     
      try {
        const updatedState = await apiService.checkBookmarkStatus(numericId);
        setIsBookmarked(updatedState);
      } catch (recheckError) {
        console.warn('  :', recheckError);
      }
      
    } finally {
      setBookmarkLoading(false);
      console.log('   ');
    }
  };

  //  URL  
  const handleExternalLink = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    
    if (tool.url && tool.url.trim() !== '') {
      // URL http://  https://   https:// 
      let url = tool.url.trim();
      if (!url.startsWith('http://') && !url.startsWith('https://')) {
        url = 'https://' + url;
      }
      
      console.log('   :', url);
      window.open(url, '_blank', 'noopener,noreferrer');
    } else {
      console.warn('   URL:', tool.url);
      alert('  URL .');
    }
  };

  // BEST 
  const getBestBadge = (rank: number) => {
    return (
      <span className="inline-flex items-center px-2 py-1 font-bold"
            data-best-badge
            style={{ 
              backgroundColor: '#F2EEFB', 
              color: '#7E50D1',
              width: '80px',
              height: '38px',
              borderRadius: '8px',
              justifyContent: 'center',
              fontSize: '16px',
              fontFamily: 'Pretendard'
            }}>
        BEST {rank}
      </span>
    );
  };

  // DB tags    
  const getDisplayTag = () => {
    // 1: tags       (DB tags  )
    if (Array.isArray(tool.tags) && tool.tags.length > 0) {
      const firstTag = tool.tags[0];
      if (firstTag && firstTag !== '') {
        return firstTag; // "AI "     
      }
    }
    
    // 2: tags   (  )
    if (typeof tool.tags === 'string' && tool.tags !== '') {
      return tool.tags;
    }
    
    // 3: categoryLabel fallback  (  )
    return tool.categoryLabel || 'AI ';
  };

  return (
    <div className={`bg-white rounded-xl group ${className || ''}`} 
         style={{ 
           border: '1px solid #DBCBF9', 
           fontFamily: 'Pretendard', 
           padding: '24px 24px 24px 24px', 
           height: '100%',
           minHeight: '280px',
           cursor: 'pointer',
           display: 'flex',
           flexDirection: 'column',
           overflow: 'hidden'
         }}
         onClick={handleCardNavigation}
         onMouseEnter={(e) => {
           e.currentTarget.style.backgroundColor = '#F2EEFB';
          e.currentTarget.style.border = '1px solid #DBCBF9';
          e.currentTarget.style.outline = '1px solid #F2EEFB';
          e.currentTarget.style.outlineOffset = '-1px';
           // tags  hover 
           const tagsElement = e.currentTarget.querySelector('[data-tags]');
           if (tagsElement) {
             (tagsElement as HTMLElement).style.backgroundColor = '#DBCBF9';
           }
           //   hover 
           const visitButton = e.currentTarget.querySelector('[data-visit-button]');
           if (visitButton) {
             (visitButton as HTMLElement).style.backgroundColor = '#7E50D1';
             const visitIcon = visitButton.querySelector('img');
             if (visitIcon) {
               (visitIcon as HTMLImageElement).src = '/images/Icon/Visit/32/White.svg';
             }
           }
           // BEST  hover 
           const bestBadge = e.currentTarget.querySelector('[data-best-badge]');
           if (bestBadge) {
             (bestBadge as HTMLElement).style.backgroundColor = '#E9DFFB';
           }
         }}
         onMouseLeave={(e) => {
           e.currentTarget.style.backgroundColor = '#FFFFFF';
           e.currentTarget.style.border = '1px solid #DBCBF9';
          e.currentTarget.style.outline = 'none';
           // tags    
           const tagsElement = e.currentTarget.querySelector('[data-tags]');
           if (tagsElement) {
             (tagsElement as HTMLElement).style.backgroundColor = '#E9DFFB';
           }
           //     
           const visitButton = e.currentTarget.querySelector('[data-visit-button]');
           if (visitButton) {
             (visitButton as HTMLElement).style.backgroundColor = '#E9DFFB';
             const visitIcon = visitButton.querySelector('img');
             if (visitIcon) {
               (visitIcon as HTMLImageElement).src = '/images/Icon/Visit/32/Black.svg';
             }
           }
           // BEST    
           const bestBadge = e.currentTarget.querySelector('[data-best-badge]');
           if (bestBadge) {
             (bestBadge as HTMLElement).style.backgroundColor = '#F2EEFB';
           }
         }}>
      
      {/* : , , BEST  */}
      <div className="flex items-start justify-between" style={{ marginBottom: '24px' }}>
        <div className="flex flex-col gap-3 items-start">
          {/*  */}
          <div className="flex-shrink-0">
            <div className="w-20 h-20 flex items-center justify-start overflow-hidden">
              {tool.logoUrl ? (
                <img 
                  src={tool.logoUrl} 
                  alt={`${tool.name} `}
                  className="w-full h-full object-contain"
                  onError={(e) => handleImageError(e, '/images/Logo/Logo_FINAL.svg')}
                />
              ) : (
                <span className="text-4xl"></span>
              )}
            </div>
          </div>
          
          {/* DB tags    */}
          <span className="inline-flex items-center px-4 py-1.5 text-body3 font-medium" 
                data-tags
                style={{ 
                  backgroundColor: '#E9DFFB',
                  borderRadius: '100px',
                  color: '#6238AE',
                  width: 'fit-content'
                }}>
            {getDisplayTag()}
          </span>
        </div>

        {/* : BEST ,   (MyPage),   */}
        <div className="flex items-start gap-3 flex-shrink-0">
          {/* BEST  */}
          {rank && rank <= 3 && getBestBadge(rank)}

          {/*   - MyPage  */}
          {isMyPage && isAuthenticated && (
            <button
              onClick={handleBookmarkToggle}
              disabled={bookmarkLoading}
              className={`flex items-center justify-center ${
                bookmarkLoading ? 'opacity-50 cursor-not-allowed' : ''
              }`}
              style={{ 
                backgroundColor: isBookmarked ? '#E9DFFB' : '#F3F4F6', 
                width: '40px', 
                height: '40px',
                borderRadius: '3.56px'
              }}
              title={isBookmarked ? ' ' : ' '}
              onMouseEnter={(e) => {
                if (!bookmarkLoading) {
                  e.currentTarget.style.backgroundColor = '#DBCBF9';
                }
              }}
              onMouseLeave={(e) => {
                if (!bookmarkLoading) {
                  e.currentTarget.style.backgroundColor = isBookmarked ? '#E9DFFB' : '#F3F4F6';
                }
              }}
            >
              {bookmarkLoading ? (
                <div className="w-5 h-5 border-2 border-current border-t-transparent rounded-full animate-spin" />
              ) : (
                <img src="/images/Icon/Save/Filled/32/Purple_Filled.svg" alt="" width={24} height={24} />
              )}
            </button>
          )}

          {/*   -  URL  */}
          <button
            onClick={handleExternalLink}
            className="flex items-center justify-center" 
            data-visit-button
            style={{ 
              backgroundColor: '#E9DFFB', 
              width: '40px', 
              height: '40px',
              borderRadius: '8px'
            }}
            title={`${tool.name}   `}
          >
            <img src="/images/Icon/Visit/32/Black.svg" alt="" width={40} height={40} />
          </button>
        </div>
      </div>

      {/* :   */}
      <div className="text-left flex-1 flex flex-col justify-end">
        <h3 className="text-title0 mb-2 line-clamp-1 text-left text-black" style={{ fontSize: '26px', lineHeight: '34px' }}>
            {tool.name}
          </h3>
        <p className="line-clamp-2 text-left"
          style={{ 
            color: '#202020', 
            fontSize: '18px',
            lineHeight: '28px',
            fontWeight: 500,
            letterSpacing: '-0.01em'
          }}>
          {tool.description}
        </p>
      </div>
    </div>
  );
};

export default ToolCard;
