// [ ]      - AI   

import React, { useState, useEffect } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import Header from '../components/Header';
import Breadcrumb from '../components/Breadcrumb';
import FilterBar from '../components/FilterBar';
import ToolCard from '../components/ToolCard';
import { apiService } from '../services';
import type { FilterType, AITool } from '../types';

//    
const getHorizontalPadding = (): number => {
  if (window.innerWidth >= 1440) {
    return 200;
  } else if (window.innerWidth >= 1024) {
    return 64; // lg:px-16
  } else if (window.innerWidth >= 768) {
    return 32; // md:px-8
  } else if (window.innerWidth >= 640) {
    return 24; // sm:px-6
  } else {
    return 16; // 
  }
};

// SearchParams   
interface SearchApiParams {
  q?: string;
  category?: string;
  pricing?: string;
  sort?: string;
  page?: number;
  size?: number;
}

const SearchPage: React.FC = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();

  //  
  const [searchQuery, setSearchQuery] = useState(searchParams.get('q') || '');
  const [searchResults, setSearchResults] = useState<AITool[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [totalCount, setTotalCount] = useState(0);
  
  //  
  const [activeFilter, setActiveFilter] = useState<FilterType>('all');
  const [sortType, setSortType] = useState('popular');
  const [selectedCategory, setSelectedCategory] = useState<string>('');
  const [suggestedKeywords, setSuggestedKeywords] = useState<string[]>([]);
  
  //  
  const [counts, setCounts] = useState({
    total: 0,
    free: 0,
    paid: 0,
    freemium: 0
  });
  
  //   
  const [horizontalPadding, setHorizontalPadding] = useState(getHorizontalPadding());
  
  //      
  useEffect(() => {
    const updatePadding = () => {
      setHorizontalPadding(getHorizontalPadding());
    };
    
    updatePadding();
    window.addEventListener('resize', updatePadding);
    return () => window.removeEventListener('resize', updatePadding);
  }, []);

  // URL   
  useEffect(() => {
    const query = searchParams.get('q') || '';
    const category = searchParams.get('category') || '';
    const pricing = searchParams.get('pricing') || 'all';
    const sort = searchParams.get('sort') || 'popular';

    setSearchQuery(query);
    setSelectedCategory(category);
    setActiveFilter(pricing as FilterType);
    setSortType(sort);
  }, [searchParams]);

  //  
  useEffect(() => {
    const performSearch = async () => {
      if (!searchQuery.trim()) {
        setSearchResults([]);
        setTotalCount(0);
        setSuggestedKeywords([]);
        return;
      }

      try {
        setLoading(true);
        setError(null);

        const searchApiParams: SearchApiParams = {
          q: searchQuery,
          category: selectedCategory || undefined,
          pricing: activeFilter === 'all' ? undefined : activeFilter.toUpperCase(),
          sort: sortType,
          size: 50
        };

        const response = await apiService.search(searchApiParams);
        
        console.log(' API :', response);
        console.log('response.tools:', response.tools);
        console.log('response.tools :', response.tools?.length);
        
        //  camelCase    
        const tools: AITool[] = (response.tools || []).map(tool => {
          //   
          const getPricingType = (pricingType: string): 'free' | 'paid' | 'freemium' => {
            const type = pricingType?.toLowerCase();
            if (type === 'free') return 'free';
            if (type === 'paid') return 'paid';
            return 'freemium';
          };

          return {
            id: tool.id.toString(),
            name: tool.serviceName,
            category: selectedCategory || 'search',
            description: tool.description,
            features: tool.keywords || [],
            rating: Number(tool.overallRating) || 0,
            tags: tool.keywords || [],
            url: '',
            releaseDate: '',
            company: 'Unknown',
            pricing: getPricingType(tool.pricingType),
            featured: false,
            categoryLabel: tool.categoryName,
            roles: [],
            userCount: 0,
            aiRating: Number(tool.overallRating) || 0,
            logoUrl: tool.logoUrl || '/images/Logo/Logo_FINAL.svg',
            serviceImageUrl: tool.logoUrl || '/images/Logo/Logo_FINAL.svg',
            priceImageUrl: tool.logoUrl || '/images/Logo/Logo_FINAL.svg',
            searchbarLogoUrl: tool.logoUrl || '/images/Logo/Logo_FINAL.svg'
          };
        });

        setSearchResults(tools);
        setTotalCount(response.totalCount || 0);
        setSuggestedKeywords(response.suggestedKeywords || []);
        
        console.log(' tools :', tools);
        console.log('tools :', tools.length);

        //   
        const newCounts = {
          total: tools.length,
          free: tools.filter(t => t.pricing === 'free').length,
          paid: tools.filter(t => t.pricing === 'paid').length,
          freemium: tools.filter(t => t.pricing === 'freemium').length
        };
        setCounts(newCounts);

      } catch (error) {
        console.error(' :', error);
        setError('   .    .');
        setSearchResults([]);
        setTotalCount(0);
      } finally {
        setLoading(false);
      }
    };

    performSearch();
  }, [searchQuery, selectedCategory, activeFilter, sortType]);

  // URL  
  const updateURLParams = (params: Record<string, string>) => {
    const newSearchParams = new URLSearchParams(searchParams);
    
    Object.entries(params).forEach(([key, value]) => {
      if (value) {
        newSearchParams.set(key, value);
      } else {
        newSearchParams.delete(key);
      }
    });

    setSearchParams(newSearchParams);
  };

  //   
  const handleFilterChange = (filter: FilterType) => {
    setActiveFilter(filter);
    updateURLParams({ 
      pricing: filter === 'all' ? '' : filter 
    });
  };

  const handleSortChange = (sort: string) => {
    setSortType(sort);
    updateURLParams({ sort });
  };

  //  
  const handleNewSearch = (query: string) => {
    setSearchQuery(query);
    updateURLParams({ q: query });
  };

  //  
  const handleCategoryFilter = (category: string) => {
    setSelectedCategory(category);
    updateURLParams({ category });
  };

  const breadcrumbItems = [
    { label: '' },
    ...(searchQuery ? [{ label: `"${searchQuery}"` }] : [])
  ];

  //  
  const categoryOptions = [
    { key: '', name: ' ' },
    { key: 'chatbot', name: '' },
    { key: 'text', name: '' },
    { key: 'image', name: '' },
    { key: 'video', name: '' },
    { key: 'audio', name: '/' },
    { key: 'code', name: '' },
    { key: 'productivity', name: '' },
    { key: '3d', name: '3D' }
  ];

  return (
    <div className="min-h-screen bg-white">
      <Header tabs={[]} activeTab="" onTabChange={() => {}} horizontalPadding={horizontalPadding} fullWidth />
      <main 
        className="mx-auto py-8"
        style={{ 
          maxWidth: '1440px',
          paddingLeft: horizontalPadding >= 200 ? `${horizontalPadding}px` : `${horizontalPadding}px`,
          paddingRight: horizontalPadding >= 200 ? `${horizontalPadding}px` : `${horizontalPadding}px`
        }}
      >
        {/*      */}
        {!searchQuery && <Breadcrumb items={breadcrumbItems} />}

        {/*    */}
        {searchQuery && (
          <div className="mb-8">
            <h1 
              className="font-semibold" 
              style={{ 
                color: '#202020', 
                fontWeight: 600, 
                fontSize: '24px', 
                fontFamily: 'Pretendard' 
              }}
            >
              '{searchQuery}'   
            </h1>
          </div>
        )}

        {/*   -     */}
        {!searchQuery && (
          <div className="mb-8">
            <h1 className="font-semibold mb-4" style={{ color: '#000000', fontSize: '32px', fontFamily: 'Pretendard' }}>
              AI  
            </h1>

            {/*  */}
            <div className="flex gap-4 mb-6">
              <div className="flex-1">
                <div className="relative">
                  <input
                    type="text"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    onKeyPress={(e) => {
                      if (e.key === 'Enter') {
                        handleNewSearch(searchQuery);
                      }
                    }}
                    placeholder="  AI  ..."
                    className="w-full pl-12 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent text-lg"
                    style={{ fontFamily: 'Pretendard' }}
                  />
                  <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                    <svg className="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                    </svg>
                  </div>
                </div>
              </div>
              
              <button
                onClick={() => handleNewSearch(searchQuery)}
                className="px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors font-medium"
                style={{ fontFamily: 'Pretendard' }}
              >
                
              </button>
            </div>

            {/*   */}
            <div className="flex flex-wrap gap-2 mb-4">
              {categoryOptions.map((category) => (
                <button
                  key={category.key}
                  onClick={() => handleCategoryFilter(category.key)}
                  className={`px-4 py-2 rounded-full text-sm font-medium transition-colors ${
                    selectedCategory === category.key
                      ? 'bg-purple-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                  style={{ fontFamily: 'Pretendard' }}
                >
                  {category.name}
                </button>
              ))}
            </div>
          </div>
        )}

        {/*     */}
        {searchQuery && (
          <>
            {/*   */}
            {error && (
              <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                <div className="flex items-center">
                  <svg className="h-5 w-5 text-red-400 mr-2" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                  </svg>
                  <p className="text-red-700 text-sm">{error}</p>
                </div>
              </div>
            )}

            {/*   */}
            {loading && (
              <div className="flex items-center justify-center py-12">
                <div className="text-center">
                  <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-purple-600 mx-auto mb-4"></div>
                  <p className="text-gray-600"> ...</p>
                </div>
              </div>
            )}

            {/*   */}
            {!loading && searchQuery && searchResults.length > 0 && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {searchResults.map((tool) => (
                  <ToolCard key={tool.id} tool={tool} />
                ))}
              </div>
            )}

            {/*    */}
            {!loading && searchQuery && searchResults.length === 0 && !error && (
              <div className="text-center py-12 mt-32">
                <h3 
                  className=""
                  style={{ 
                    color: '#202020', 
                    fontWeight: 600, 
                    fontSize: '18px',
                    fontFamily: 'Pretendard',
                    marginBottom: '0px'
                  }}
                >
                  '{searchQuery}'    .
                </h3>
                <p 
                  className=""
                  style={{ 
                    color: '#202020', 
                    fontWeight: 400, 
                    fontSize: '16px',
                    fontFamily: 'Pretendard',
                    marginTop: '4px'
                  }}
                >
                      .
                </p>
                
                {/*   */}
                <div 
                  className="mx-auto max-w-2xl"
                  style={{
                    backgroundColor: '#F2EEFB',
                    borderRadius: '12px',
                    padding: '16px',
                    marginTop: '65px'
                  }}
                >
                  <p 
                    className="mb-4"
                    style={{ 
                      fontWeight: 600, 
                      fontSize: '16px',
                      fontFamily: 'Pretendard'
                    }}
                  >
                    <span style={{ color: '#8B5CF6' }}></span>
                    <span style={{ color: '#202020' }}> !</span>
                  </p>
                  
                  {/*    */}
                  <div className="flex flex-wrap justify-center gap-2">
                    {[
                      '',
                      'ChatGPT', 
                      ' ',
                      ' ',
                      ' ',
                      '/',
                      '/',
                      'AI  '
                    ].map((keyword, index) => (
                      <button
                        key={index}
                        onClick={() => handleNewSearch(keyword)}
                        className="flex items-center rounded-full text-xs font-medium transition-colors whitespace-nowrap"
                        style={{
                          backgroundColor: '#FFFFFF',
                          borderRadius: '20px',
                          paddingTop: '4px',
                          paddingRight: '12px',
                          paddingBottom: '4px',
                          paddingLeft: '8px',
                          color: '#202020',
                          fontWeight: 500,
                          fontSize: '12px',
                          fontFamily: 'Pretendard',
                          width: 'fit-content',
                          columnGap: '2px'
                          // border  (stroke  )
                        }}
                        onMouseEnter={(e) => {
                          e.currentTarget.style.backgroundColor = '#DBCBF9';
                        }}
                        onMouseLeave={(e) => {
                          e.currentTarget.style.backgroundColor = '#FFFFFF';
                        }}
                      >
                        <img 
                          src="/images/Icon/Magnifier/18/Purple.svg" 
                          alt="" 
                          style={{ width: '18px', height: '18px' }}
                        />
                        <span>{keyword}</span>
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            )}
          </>
        )}

        {/*   ( ) */}
        {!searchQuery && !loading && (
          <div className="text-center py-12">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-xl font-semibold text-gray-800 mb-2">AI  </h3>
            <p className="text-gray-600 mb-6">
               AI  .    AI    .
            </p>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 max-w-2xl mx-auto text-sm">
              <div className="p-4 bg-gray-50 rounded-lg">
                <div className="font-medium text-gray-800 mb-1"> </div>
                <div className="text-gray-600">ChatGPT, Midjourney, Gamma</div>
              </div>
              <div className="p-4 bg-gray-50 rounded-lg">
                <div className="font-medium text-gray-800 mb-1"></div>
                <div className="text-gray-600"> ,  </div>
              </div>
              <div className="p-4 bg-gray-50 rounded-lg">
                <div className="font-medium text-gray-800 mb-1"></div>
                <div className="text-gray-600">, , </div>
              </div>
              <div className="p-4 bg-gray-50 rounded-lg">
                <div className="font-medium text-gray-800 mb-1"></div>
                <div className="text-gray-600">, , </div>
              </div>
            </div>
          </div>
        )}
      </main>
    </div>
  );
};

export default SearchPage;